.carousel {
  margin: 0 0 20px;
  height: 515px;
}

.carousel-header {
  margin: 5px auto;
  width: 70%;
}

.carousel-title {
  font-size: 40px;
  color: rgb(196, 196, 196);
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;  
}

.carousel-title:hover {
  color: #FFF;
  font-size: 41px;
  text-decoration: none;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}

.carousel-overview {
  margin: auto;
  width: 70%;
  font-size: 18px;
}


